@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  src: local("Muller light"), url("../fonts/mullerlight-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "PlayfairDisplay";
  font-style: normal;
  font-weight: 700;
  src: local("PlayfairDisplay bold"), url("../fonts/playfair_display_bold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: local("Raleway light"), url("../fonts/raleway-light-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  src: local("Raleway semibold"), url("../fonts/raleway-semibold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: local("Raleway extrabold"), url("../fonts/raleway-extrabold-webfont.woff2") format("woff2");
}

body,
html {
  margin: 0;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body {
  font-family: "Raleway", "Muller", "PlayfairDisplay", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 1px;
  color: #5e5e5e;
  background-color: #bbb;
  /* #fdfaf3 */
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.wrapper {
  max-width: 1170px;
  margin: 0 37px;
}

.slider-button,
.button-arrow,
.play,
.menu {
  font-size: 0;
  cursor: pointer;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #7d7d7d;
  font-size: 1.125em;
  line-height: 30px;
}

.submit-orange {
  cursor: pointer;
}

.phone-number {
  font-family: "Muller";
  color: #252525;
  font-size: 0.875em;
  letter-spacing: 1.5px;
}

.intro-text {
  color: #ffffff;
  font-size: 0.875em;
  line-height: 20px;
}

h1 {
  font-family: "PlayfairDisplay";
  font-size: 3.938em;
  line-height: 58px;
  color: #ffffff;
  letter-spacing: 1.5px;
}

.button-hover,
.submit-orange {
  display: block;
  font-weight: 700;
  font-size: 0.938em;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
}

.button-hover {
  border: 2px solid #fff;
}

.button-hover:hover {
  background-color: #f8a25a;
  text-decoration: none;
  color: #fff;
}

.button-hover:active {
  background-color: #ef862f;
}

.headline {
  font-family: "PlayfairDisplay";
  color: #00203a;
  font-size: 3em;
}

.navigation-ref {
  text-transform: uppercase;
  font-size: 1.25em;
  line-height: 42px;
}

.features-list .feature-item .feature-headline {
  font-weight: 500;
  font-size: 1.375em;
  color: #1a292f;
}

.current-ref,
.services-ref.current,
.service-column span {
  color: #00203a;
}

.service-headline {
  font-family: "PlayfairDisplay";
  font-weight: 400;
  font-size: 1.875em;
  color: #00203a;
}

.service-column span,
.service-text {
  font-size: 1.125em;
  line-height: 30px;
}

.video-text {
  font-family: "Raleway";
  font-size: 1.125em;
  color: #ffffff;
}

.steps-headline {
  font-weight: 500;
  font-size: 1.375em;
  line-height: 17px;
  color: #252525;
}

.steps-number {
  font-size: 0.75em;
}

.form-headline {
  font-family: "PlayfairDisplay";
  font-size: 2.813em;
  color: #ffffff;
}

.form-text,
.user-text,
.form-text2 {
  font-size: 0.875em;
  line-height: 20px;
  color: #ffffff;
}

.client-name {
  font-weight: 500;
  font-size: 1.375em;
  line-height: 30px;
  color: #252525;
}

.feedbacks-name {
  font-weight: 700;
  color: #efb172;
}

.sgn1,
.sgn2 {
  color: #ffc487;
}

.main-header {
  padding-top: 83px;
  margin-bottom: 216px;
  display: flex;
  justify-content: center;
}

.main-header .wrapper {
  position: relative;
}

.main-header .wrapper .slider-box {
  position: relative;
}

.main-header .wrapper .slider-box .slider-wrap .main-slider {
  width: 100%;
}

.main-header .wrapper .slider-box .main-bg {
  position: absolute;
  top: -83px;
  left: -100px;
}

.main-header .wrapper .slider-box .slider-buttons {
  position: absolute;
  right: 6px;
  bottom: -47px;
}

.main-header .wrapper .slider-box .slider-buttons .slider-button {
  border: none;
  height: 3px;
  width: 26px;
  margin-left: 7px;
  background-color: #cdd3cf;
}

.main-header .wrapper .slider-box .slider-buttons .slider-button.current {
  background-color: #1a444a;
}

.main-header .wrapper .slider-box .slider-buttons .slider-button:hover {
  background-color: #1a444a;
}

.main-header .wrapper .intro-column {
  position: absolute;
  max-width: 325px;
  top: 170px;
  left: 94px;
}

.main-header .wrapper .intro-column .intro-text {
  margin-top: 26px;
  word-wrap: break-word;
}

.main-header .wrapper .intro-column .button-hover {
  margin-top: 35px;
  padding: 20px 78px 17px 28px;
  max-width: 278px;
  position: relative;
}

.main-header .wrapper .intro-column .button-hover::after {
  content: "";
  position: absolute;
  background-image: url(../img/Arrow-OutRight-icon.png);
  background-repeat: no-repeat;
  background-position-x: 18px;
  background-position-y: 2px;
  height: 22px;
  width: 100px;
}

.main-header .wrapper .menu-wrap {
  position: absolute;
  left: 2px;
  top: -54px;
}

.main-header .wrapper .menu-wrap .show-nav {
  display: block;
  background-image: url("../img/bg.png");
  position: absolute;
  top: 54px;
  left: 0;
  width: 1161px;
  height: 595px;
}

.main-header .wrapper .navigation-wrap {
  display: none;
}

.main-header .wrapper .navigation-wrap .navigation-list {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding-top: 110px;
}

.main-header .wrapper .navigation-wrap .navigation-list .navigation-item {
  margin-top: 12px;
}

.main-header .wrapper .navigation-wrap .navigation-list .navigation-item .navigation-ref:hover {
  color: #f8a25a;
}

.main-header .wrapper .navigation-wrap .navigation-list .navigation-item .navigation-ref:active {
  color: #ffffff;
}

.main-header .logo {
  position: absolute;
  top: -61px;
  left: 51%;
  transform: translateX(-50%);
}

.main-header .phone-wrap {
  position: absolute;
  top: -58px;
  right: 4px;
}

.main-header .phone-wrap:hover span {
  font-weight: 700;
}

.main-header .phone-wrap:hover svg {
  animation: phone-animation 1s infinite ease-in;
}

@keyframes phone-animation {
  0% {
    transform: scale(2);
  }

  50% {
    transform: rotate(360deg);
  }

  100% {
    transform: scale(2);
  }
}

.main-header .social-list {
  display: flex;
  position: absolute;
  left: 0;
  bottom: -56px;
}

.main-header .social-list .social-item {
  margin-right: 36px;
}

.main-header .social-list .social-item:hover svg {
  transform: scale(1.6);
}

.main-header .social-list .social-item:active svg {
  opacity: 0.3;
}

.main-header .social-list .social-item .social-ref {
  display: block;
}

.features {
  display: flex;
  justify-content: center;
  margin-bottom: 112px;
}

.features-list {
  display: flex;
  flex-wrap: wrap;
}

.feature-item {
  width: 25%;
  padding-right: 15px;
}

.feature-item:nth-child(2),
.feature-item:nth-child(4) {
  margin-top: 210px;
}

.feature-item-wrap .number {
  display: block;
  color: #fde3c9;
  font-family: "PlayfairDisplay";
  font-size: 85px;
  margin-bottom: 40px;
}

.feature-item-wrap .feature-headline {
  display: block;
  max-width: 280px;
  margin-bottom: 12px;
  letter-spacing: 0;
}

.portfolio .wrapper .headline {
  margin-bottom: 66px;
}

.portfolio .wrapper .portfolio-nav {
  display: flex;
  flex-wrap: wrap;
}

.portfolio .wrapper .portfolio-nav .portfolio-item {
  margin-right: 45px;
}

.portfolio .wrapper .portfolio-nav .portfolio-item .current-ref {
  font-weight: 600;
  border-bottom: 1px solid #000;
  padding-bottom: 10px;
}

.service-column .button-white {
  max-width: 210px;
  padding: 16px 0;
  color: #00203a;
  border: 2px solid #00203a;
  text-align: center;
}

.service-column .button-white:hover {
  color: #ffffff;
  border-color: #ffffff;
}

.feedbacks .wrapper {
  position: relative;
}

@media screen and (max-width: 1199px) {
  .menu-wrap,
  .social-list {
    padding-left: 15px;
  }
}

@media (min-width: 769px) and (max-width: 900px) {
  .feature-item {
    width: 50%;
    padding-right: 110px;
  }

  .feature-text {
    font-size: 18px;
  }
}

@media (min-width: 579px) and (max-width: 768px) {
  .main-header .wrapper .phone-wrap,
  .main-header .wrapper .slider-buttons,
  .main-header .wrapper .social-list {
    display: none;
  }

  .main-slider,
  .main-bg {
    display: none;
  }

  .features {
    margin-bottom: 155px;
  }

  .feature-item {
    width: 50%;
    padding-right: 72px;
  }

  .feature-item:nth-child(4) {
    margin-top: 170px;
  }

  .feature-item:nth-child(2) {
    margin-top: 180px;
  }

  .feature-text {
    font-size: 1.125em;
  }
}

@media (min-width: 320px) and (max-width: 578px) {
  .main-header {
    background-image: url("../img/320/bg-320.jpg");
    height: 320px;
    width: 608px;
    background-position: 0 0;
    position: relative;
  }

  .main-header .wrapper .phone-wrap,
  .main-header .wrapper .slider-buttons,
  .main-header .wrapper .social-list {
    display: none;
  }

  .main-slider,
  .main-bg {
    display: none;
  }

  .features .wrapper {
    margin: 0 15px;
  }

  .features .wrapper .feature-item {
    width: 100%;
    margin-top: 40px;
  }

  .features .wrapper .feature-item .feature-item-wrap .feature-headline {
    font-size: 1.5em;
  }

  .features .wrapper .feature-item .feature-item-wrap .feature-text {
    font-size: 1.125em;
  }
}